import React, { useState } from 'react'
import './userList.css'
import { DataGrid } from '@mui/x-data-grid';
import {DeleteOutline} from '@mui/icons-material';
import {userRows} from '../../dummyData'
import { Link } from 'react-router-dom';

export default function UserList() {
    const [data, setData] = useState(userRows)

    const deleteHandler = (id) => {
        return setData(data.filter((item) => item.id !== id))
    }

    const columns = [
        { field: 'id', headerName: 'userId', width: 100 },
        { field: 'user', headerName: 'Username', width: 200, renderCell: (params) => {
            return (
                <div className='userListUser'>
                    <img src={params.row.avatar} alt="" className="userListImg" />
                    {params.row.username}
                </div>
            )
        } },
        { field: 'email', headerName: 'Email', width: 200 },
        {
          field: 'status',
          headerName: 'Status',
          width: 160,
        },
        {
          field: 'transaction',
          headerName: 'Transaction Volume',
          width: 160,
        },
        {
            field: 'action',
            headerName: 'Action',
            width: 160,
            renderCell: (params) => {
                return (
                    <>
                      <Link to={'/user/'+params.row.id}>
                      <button className="userListEdit">Edit</button>
                      </Link>
                      <DeleteOutline className="userListDelete" onClick={() => deleteHandler(params.row.id)} />
                    </>
                )
            }
        }
      ];
    return (
        <div className='userList'>
            <DataGrid rows={data} disableSelectionOnClick columns={columns} pageSize={8} rowsPerPageOptions={[8]} checkboxSelection />
        </div>
    )
}
