import React from 'react'
import './productsList.css'
import { DataGrid } from '@mui/x-data-grid';
import {DeleteOutline} from '@mui/icons-material';
import {productRows} from '../../dummyData'
import { Link } from 'react-router-dom';
import { useState } from 'react';

export default function ProductsList() {
    const [data, setData] = useState(productRows)

    const deleteHandler = (id) => {
        return setData(data.filter((item) => item.id !== id))
    }
    
    const columns = [
        { field: 'id', headerName: 'productId', width: 100 },
        { field: 'product', headerName: 'Product', width: 200, renderCell: (params) => {
            return (
                <div className='productListProduct'>
                    <img src={params.row.img} alt="" className="productListImg" />
                    {params.row.productname}
                </div>
            )
        } },
        { field: 'stock', headerName: 'Stock', width: 200 },
        {
          field: 'status',
          headerName: 'Status',
          width: 160,
        },
        {
          field: 'price',
          headerName: 'Price',
          width: 160,
        },
        {
            field: 'action',
            headerName: 'Action',
            width: 160,
            renderCell: (params) => {
                return (
                    <>
                      <Link to={'/product/'+params.row.id}>
                      <button className="productListEdit">Edit</button>
                      </Link>
                      <DeleteOutline className="productListDelete" onClick={() => deleteHandler(params.row.id)} />
                    </>
                )
            }
        }
      ];
    return (
        <div className='productsList'>
            <DataGrid rows={data} disableSelectionOnClick columns={columns} pageSize={8} rowsPerPageOptions={[8]} checkboxSelection />
        </div>
    )
}
